import Vue from 'vue'
import Router from 'vue-router'
import menus from './menu';
Router.prototype.goBack = function () {
    this.isBack = true;
    window.history.go(-1);
};
Vue.use(Router);
const router = new Router({
    base: process.env.BASE_URL,
    routes: [
        //
        {
            path: '/',
            name: 'applogin',
            component: () => import( './pages/Login.vue'),
            meta: {title: ''},
        },
        {
            path: '/loginapp/:id/:token',
            name: 'loginapp',
            component: () => import( './pages/LoginApp.vue'),
            meta: {title: ''},
        },
        {
            path: '/frame',
            name: 'adminframe',
            component: () => import( './pages/Frame.vue'),
            meta: {title: ''},
            children: menus.menuRouter
        },

        {
            path: '/design/:pageid',
            name: 'designpage',
            component: () => import( './pages/setting/style/Design.vue'),
            meta: {title: ''},
        },

        // 
         {
             path: '*',
             component: () => import( './pages/Nopage.vue'),
             meta: {
                 title: '()',
             },
         },
    ]
});


export default router;
