import router from './router'
import store from './store/'  // token   
import NProgress from 'nprogress' //
import 'nprogress/nprogress.css'//
const whiteList = ['loginapp','applogin']; //
const title = '';
NProgress.configure({showSpinner: false});
/**/
router.beforeEach((to, from, next) => {
    NProgress.start();
    let token = store.getters.appToken;
    let appInfo   = store.getters.appInfo;
     if (to.meta.title) {
         document.title = to.meta.title + '-' + title
    }
    next();
    if(token){
    
        let is_auth = true;
            if(is_auth){
                next();
                NProgress.done();
            }else{
                next(`/noauth`);
                NProgress.done();
            }
    }else {
        if (whiteList.indexOf(to.name) !== -1) { // 
            next();
            NProgress.done();
        } else {
            next(`/`); // 
            NProgress.done();
        }
    }
});
/**/
router.afterEach(() => {
    NProgress.done();
});
